body {
  background: #101928;
}

.heartbeat {
  animation-duration: 2s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-name: heartbeat;
}

@keyframes heartbeat {
  0% {
    transform: scale3d(1, 1, 1);
  }
  20% {
    opacity: 0.7;
    transform: scale3d(0.8, 0.8, 0.8);
  }
  40% {
    opacity: 1;
    transform: scale3d(1.5, 1.5, 1.5);
  }
  60% {
    opacity: 0.7;
    transform: scale3d(1, 1, 1);
  }
  70% {
    opacity: 1;
    transform: scale3d(1.5, 1.5, 1.5);
  }
  100% {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }
}

